import { useContext, useEffect, useState } from "react";
import axios from "axios";
import {
  MessageContext,
  handleFailMessage,
  handleSuccessMessage,
} from "../store/messageStore";

export default function OrderModal({ closeOrderModal, getOrders, tempOrder }) {
  const [tempData, setTempData] = useState({});

  const [, dispatch] = useContext(MessageContext);

  useEffect(() => {
    setTempData(tempOrder);
  }, [tempOrder]);

  const handleChange = (e) => {
    // console.log(e);
    // console.log("onChange", e);
    const { value, name } = e.target;

    setTempData({ ...tempData, [name]: value });
    // console.log("", tempData);
  };

  const submit = async () => {
    try {
      const res = await axios.put(
        `${process.env.REACT_APP_API_URL}/v2/api/${process.env.REACT_APP_API_PATH}/admin/order/${tempOrder.id}`,
        {
          data: tempData,
        }
      );
      // console.log(res);
      if (res.data.success) {
        handleSuccessMessage(dispatch, res);
      } else {
        handleFailMessage(dispatch, res);
      }

      closeOrderModal(); //  Modal
      getOrders(); //  API request
    } catch (error) {
      console.log(error);
    }
  };

  return (
    <>
      <div
        className="modal fade"
        tabIndex="-1"
        aria-labelledby="exampleModalLabel"
        id="orderModal"
      >
        <div className="modal-dialog modal-lg">
          <div className="modal-content">
            <div className="modal-header">
              <h1 className="modal-title fs-5 fw-bold" id="exampleModalLabel">
                 : {tempData.id}
              </h1>
              <button
                type="button"
                className="btn-close"
                aria-label="Close"
                onClick={closeOrderModal}
              />
            </div>
            <div className="modal-body">
              <div className="row">
                <div className="col-md-8">
                  <table className="table">
                    <thead>
                      <tr>
                        <th scope="col"></th>
                        <th scope="col"></th>
                        <th scope="col"></th>
                        <th scope="col"></th>
                      </tr>
                    </thead>
                    <tbody>
                      <tr>
                        <td>
                          {tempData?.products &&
                            Object.values(tempData?.products)?.map((item) => {
                              return (
                                <ul key={item.id} className="list-unstyled">
                                  <li>{item.product.title}</li>
                                </ul>
                              );
                            })}
                        </td>
                        <td>
                          {tempData?.products &&
                            Object.values(tempData?.products)?.map((item) => {
                              return (
                                <ul key={item.id} className="list-unstyled">
                                  <li className="fw-bold text-red">
                                    x{item.qty}
                                  </li>
                                </ul>
                              );
                            })}
                        </td>
                        <td>
                          {tempData?.products &&
                            Object.values(tempData?.products)?.map((item) => {
                              return (
                                <ul key={item.id} className="list-unstyled">
                                  <li>$ {item.total - item.final_total}</li>
                                </ul>
                              );
                            })}
                        </td>
                        <td>
                          {tempData?.products &&
                            Object.values(tempData?.products)?.map((item) => {
                              return (
                                <ul key={item.id} className="list-unstyled">
                                  <li>$ {tempData.total - item.final_total}</li>
                                </ul>
                              );
                            })}
                        </td>
                      </tr>
                      <tr>
                        <td></td>
                        <th></th>
                        <td>$ {(tempData.total / 0.8) * 0.2}</td>
                        <td>$ {tempData.total}</td>
                      </tr>
                    </tbody>
                  </table>
                </div>
                {/*  */}
                <div className="col-md-3 p-2 d-flex justify-content-center">
                  <div>
                    <h4 className="form-label fw-bold"></h4>
                    <div className="form-check">
                      <input
                        className="form-check-input"
                        type="radio"
                        name="message"
                        id="not-yet-delivery"
                        value=""
                        checked={tempData.message === ""}
                        onChange={(e) => handleChange(e)}
                      />
                      <label
                        className="form-check-label"
                        htmlFor="not-yet-delivery"
                      >
                        
                      </label>
                    </div>
                    <div className="form-check">
                      <input
                        className="form-check-input"
                        type="radio"
                        name="message"
                        id="delivering"
                        value=""
                        checked={tempData.message === ""}
                        onChange={(e) => handleChange(e)}
                      />
                      <label className="form-check-label" htmlFor="delivering">
                        
                      </label>
                    </div>
                    <div className="form-check">
                      <input
                        className="form-check-input"
                        type="radio"
                        name="message"
                        id="finish"
                        value=""
                        checked={tempData.message === ""}
                        onChange={(e) => handleChange(e)}
                      />
                      <label className="form-check-label" htmlFor="finish">
                        
                      </label>
                    </div>
                  </div>
                </div>
              </div>

              {/*  */}
              <div className="row">
                <div className="col-md-8">
                  <h5 className="fw-bold"></h5>
                  <ul className="list-unstyled p-1 mb-0">
                    <li>: {tempData?.user?.name}</li>
                    <li>: {tempData?.user?.tel}</li>
                    <li>: {tempData?.user?.email}</li>
                    <li>: {tempData?.user?.address}</li>
                  </ul>
                </div>
              </div>
            </div>
            <div className="modal-footer">
              <button
                type="button"
                className="btn btn-secondary"
                onClick={closeOrderModal}
              >
                
              </button>
              <button
                type="button"
                className="btn btn-primary"
                onClick={submit}
              >
                
              </button>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}
